.page-made {
  padding-top: 125px;
  padding-bottom: 145px;
  background-color: $section-bcg-color;

  @media screen and (min-width: $tablet) {
    padding-top: 165px;
    padding-bottom: 212px;
  }

  @media screen and (min-width: $desktop) {
    padding-top: 125px;
    padding-bottom: 310px;
  }
}

.page-made .subtitle {
  font-size: 18px;
  margin-bottom: 15px;
  color: $secondary-title-color;
  font-weight: 400;
  line-height: 1.17;
  letter-spacing: 0.04em;

  @media screen and (min-width: $tablet) {
      font-size: 26px;
  }

  @media screen and (min-width: $desktop) {
      font-size: 30px;
  }
}

.page-made .title {
  font-size: 34px;
  margin-bottom: 26px;
  color: $primary-title-color;
  font-weight: 400;
  line-height: 1.15;
  letter-spacing: 0.04em;

  @media screen and (min-width: $tablet) {
    margin-bottom: 50px;
    font-size: 48px;
  }

  @media screen and (min-width: $desktop) {
    margin-bottom: 55px;
    font-size: 58px;
  }
}

.made {
  margin-bottom: 38px;

  @media screen and (min-width: $tablet) {
    display: flex;
    margin-bottom: 66px;
  }

  @media screen and (min-width: $desktop) {
      position: relative;
      margin-bottom: 40px;
      z-index: 2;
  }
      
}

