.made .thumb {
    margin-bottom: 46px;
  
    @media screen and (min-width: $tablet) and (max-width: $max-tablet) {
        min-width: 340px;
        margin-bottom: 0;
    }
  }
  
  @media screen and (min-width: $desktop) {
      .made .thumb {
          min-width: 516px;
          margin-bottom: 0;
      }
  }

.made-paragraph {
    padding-top: 50px;

    @media screen and (min-width: $tablet) {
        margin-left: 17px;
    }

    @media screen and (min-width: $desktop) {
        margin-left: 74px;
        // position: absolute;
    }
}

.made-text {
  margin-top: 0;
  margin-bottom: 30px;
  padding-right: 30px;
  padding-left: 30px;
  @include font-data(12px, $primery-text-color, 500, 1.62, 0.04em);

  @media screen and (max-width: $max-mobile) {
    position: relative;
  }

  @media screen and (min-width: $tablet) {
    //   margin-left: 17px;
      margin-bottom: 52px;
      padding-right: 0;
      padding-left: 0;
      font-size: 14px;
      
  }

  @media screen and (min-width: $desktop) {
    //   margin-left: 74px;
      margin-bottom: 45px;
      font-size: 16px;
    //   position: absolute;
  }
}

.made-text p {
  margin-top: 0;
  margin-bottom: 30px;
}

.made-text-secondary {
  margin: 0;
  font-family: $font-DM-Sans;
 
  @include font-data(12px, $secondary-text-color, 500, 1.62, 0.04em);

  @media screen and (min-width: $tablet) {
    font-size: 14px;
    line-height: 1.9;
  }
}


.made-text-secondary p {
  margin-bottom: 15px;
}

.text-background {
  position: absolute;
  bottom: 0;
  left: -20px;
  width: 320px;

  @media screen and (min-width: $tablet) and (max-width: $max-tablet) {
      display: none;
  }

  @media screen and(min-width: $desktop) {
      min-width: 1280px;
      height: 535px;
      top: 50px;
      left: -115px;
      z-index: 1;
    //   bottom: -50px;
    //   right: -115px;
  }
}

.made-button {
  display: flex;
  margin-left: 30px;
  width: 176px;
  height: 44px;
  background-color: $white-color;
  border-radius: 22px;

  @include font-data(14px, $primary-title-color, 700, 1.3, 0.04em);

  &:hover,
  &:focus {
    background-color: #ecebeb;
  }

  @media screen and (min-width: $tablet) {
      margin-left: 0;
      font-size: 16px;
  }
}

.button-link {
  width: 100%;
  height: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: $primary-title-color;
}

.button-icon {
  margin-left: 7px;
  fill: currentColor;
}

.made-features-list {
  margin: 0;
  padding-left: 0;
  padding-right: 50px;

  @media screen and (min-width: $tablet) {
      display: flex;
      padding-right: 0;
  }
}

.made-features-item:not(:last-child) {
  margin-bottom: 38px;

  @media screen and (min-width: $tablet) {
      margin-bottom: 0;
      margin-right: 60px;
  }
}

.made-features-item img {
  margin-bottom: 16px;

  @media screen and (min-width: $tablet) {
      margin-bottom: 36px;
  }
}

.made-features-item span {
  font-family: $font-Titan-One;
  @include font-data(36px, $primary-title-color, 400, 1.13, 0.04em);

  @media screen and (min-width: $tablet) {
    font-size: 48px;
}
}

.made-features-item p {
  margin-top: 15px;
  margin-bottom: 0;
  @include font-data(12px, $primery-text-color, 500, 1.62, 0.04em);

  @media screen and (min-width: $tablet) {
      font-size: 14px;
  }
}
